<template>
  <!-- 报表 -->
  <div class="main_con_box">
    <div style="position: relative;">
      <el-tabs class="eltabs" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane class="report" label="跟进统计" name="followStatistics"
          ><followStatistics ref="followStatistics"></followStatistics
        ></el-tab-pane>
        <el-tab-pane class="report" label="签约统计" name="signingStatistics"
          ><signingStatistics ref="signingStatistics"></signingStatistics
        ></el-tab-pane>
        <el-tab-pane class="report" label="回款统计" name="returnStatistics"
          ><returnStatistics ref="returnStatistics"></returnStatistics
        ></el-tab-pane>
        <el-tab-pane class="report" label="发货统计" name="shippingStatistics"
          ><shippingStatistics ref="shippingStatistics"></shippingStatistics
        ></el-tab-pane>
        <el-tab-pane
          class="report"
          label="新增客户统计"
          name="customerStatistics"
          ><customerStatistics ref="customerStatistics"></customerStatistics
        ></el-tab-pane>
      </el-tabs>
      <el-button v-if="isouter==1" size="mini" @click="returnrouter" style="position: absolute;right:15px;top:10px;">
          返回
      </el-button>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from "vue";
import followStatistics from "./followStatistics/followStatistics.vue";
import signingStatistics from "./signingStatistics/signingStatistics.vue";
import returnStatistics from "./returnStatistics/returnStatistics.vue";
import shippingStatistics from "./shippingStatistics/shippingStatistics.vue";
import customerStatistics from "./customerStatistics/customerStatistics.vue";
export default {
  components: {
    followStatistics,
    signingStatistics,
    returnStatistics,
    shippingStatistics,
    customerStatistics,
  },
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
      activeName: "followStatistics",
      isouter:null
    });
    const handleClick = (tab, event) => {
      console.log(tab, event);
    };
    const returnrouter = () => {
      proxy.$router.go(-1)
    }
    onMounted(()=>{
      if(proxy.$route.query.str&&proxy.$route.query.str!=''){
        proxy.activeName = proxy.$route.query.str
      }else{
        proxy.activeName = 'followStatistics'
      }
      proxy.isouter = proxy.$route.query.isouter
    })
    return {
      ...toRefs(state),
      handleClick,
      returnrouter
    };
  },
};
</script>

<style scoped>
.main_con_box {
  background-color: #fff;
}
.report {
  background-color: #f2f2f2;
}
</style>
<style>
.el-tabs__nav-wrap {
  /* padding: 0 15px; */
  padding-left: 30px;
  padding-right: 15px;
  box-sizing: border-box;
}
.eltabs.el-tabs .el-tabs__header {
  padding-top: 10px !important;
}
.el-tabs__header {
  margin: 0;
}
</style>