<template>
  <!-- 按产品同比环比 -->
  <div class="top">
    <div class="top_line">
      <div>时间段：</div>
      <p>日</p>
      <p>周</p>
      <p>月</p>
      <p>季</p>
      <p>年</p>
    </div>
    <div class="top_line">
      <div>比较时间:</div>
      <div>
        <el-date-picker
          size="mini"
          v-model="time1"
          type="date"
          placeholder="选择日期"
        ></el-date-picker>
      </div>
      <div>
        <el-date-picker
          size="mini"
          v-model="time2"
          type="date"
          placeholder="选择日期"
        ></el-date-picker>
      </div>
    </div>
    <div class="top_line">
      <div>产品分类:</div>
      <div>
        <el-select size="mini" v-model="value1" placeholder="请选择">
          <el-option
            v-for="item in options1"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="top_line">
      <el-checkbox v-model="checked1" label="只显示有数据的产品"></el-checkbox>
    </div>
  </div>
  <div id="ship_myEcharts4" :style="{ width: '900px', height: '400px' }"></div>
  <div class="footer">
    <div class="footer_title">
      <p>合计：</p>
      <p>销售额：</p>
      <p>￥31000.00</p>
    </div>
    <div class="footer_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%; height: 100%"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="product" label="产品" width="">
        </el-table-column>
        <el-table-column
          prop="shipment_quantity"
          label="发货数量（2021-09-09）"
          width=""
        >
        </el-table-column>
        <el-table-column
          prop="shipment_quantity"
          label="发货数量（2021-09-10）"
          width=""
        >
        </el-table-column>
      </el-table>
    </div>
  </div>
  <div class="last">共 4 条</div>
</template>
<script>
import { reactive, toRefs, onMounted, onUnmounted } from "vue";
import * as echarts from "echarts";
export default {
  name: "echartsBox",
  components: {},
  setup() {
    const state = reactive({
      value1: "",
      options1: [],
      checked1: false,
      time1: "",
      time2: "",
      tableData: [
        {
          product: "",
          shipment_quantity: "",
        },
        {
          product: "",
          shipment_quantity: "",
        },
        {
          product: "",
          shipment_quantity: "",
        },
        {
          product: "",
          shipment_quantity: "",
        },
      ],
    });

    // 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("ship_myEcharts4"));
      // 把配置和数据放这里
      chart.setOption({
        color: ["#5B9BD5", "#ED7D31"],
        xAxis: {
          type: "category",
          axisLabel: {
            fontSize: 16,
          },
          data: ["A产品", "B产品", "C产品"],
        },
        tooltip: {
          //   trigger: "axis",
        },
        yAxis: {
          type: "value",
          min: 0,
          max: 7000,
          splitNumber: 8,
          axisLabel: { show: true },
        },
        legend: {color: ["#5B9BD5", "#ED7D31"],},  //legend是设置图例
        dataset: {
          // 提供一份数据。
          source: [
            ["product", "9月-2020年", "9月-2021年"],
            ["A产品", 4300, 1000],
            ["B产品", 1300, 2900],
            ["C产品", 6300, 2300],
          ],
        },
        series: [
          {
            barWidth: 50,
            type: "bar",
            showBackground: false,

            // smooth: true  用于折线图中让折线变平滑
          },
          {
            barWidth: 50,
            type: "bar",
            showBackground: false,
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }
    return {
      ...toRefs(state),
      initChart,
    };
  },
};
</script>

<style scoped>
.top {
  margin-left: 30px;
}
.top_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  height: 40px;
}
.top_line > p {
  margin-left: 30px;
}
.el-select,
.choose {
  margin-left: 30px;
}
/* #myEcharts {
    margin-left: ;
} */
.footer {
  margin-left: 20px;
}
.footer_title {
  display: flex;
}
.footer_main {
  margin: 0 20px 20px 0;
}
.last {
  text-align: center;
  margin-bottom: 10px;
}
</style>
<style>
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  margin-left: 30px;
}
</style>