<template>
  <!-- 按业务员排名 -->
  <div class="top">
    <div class="top_line">
      <div>时间段：</div>
      <p>全部</p>
      <p>今日</p>
      <p>本周</p>
      <p>本月</p>
      <p>本季</p>
      <p>今年</p>
      <p>自定义时间段</p>
    </div>
    <div class="top_line">
      <div>业务员</div>
      <div>
        <el-select size="mini" v-model="value1" placeholder="请选择">
          <el-option
            v-for="item in options1"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        <el-select size="mini" v-model="value2" placeholder="请选择">
          <el-option
            v-for="item in options2"
            :key="item.value2"
            :label="item.label"
            :value="item.value2"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="top_line">
      <el-checkbox v-model="checked1" label="只显示有数据的用户"></el-checkbox>
      <el-checkbox
        class="choose"
        v-model="checked2"
        label="显示未授权的用户"
      ></el-checkbox>
    </div>
  </div>
  <div id="myEcharts" :style="{ width: '900px', height: '400px' }"></div>
  <div class="footer">
    <div class="footer_title">
      <p>合计：</p>
      <p>跟进数：</p>
      <p>12</p>
    </div>
    <div class="footer_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%; height: 100%"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="ranking" label="排名" width="">
        </el-table-column>
        <el-table-column prop="salesman" label="业务员" width="">
        </el-table-column>
        <el-table-column prop="follow_number" label="跟进数" width="">
        </el-table-column>
      </el-table>
    </div>
  </div>
  <div class="last">共 4 条</div>
</template>
<script>
import { reactive, toRefs, onMounted, onUnmounted } from "vue";
import * as echarts from "echarts";
export default {
  name: "echartsBox",
  components: {},
  setup() {
    const state = reactive({
      value1: "",
      options1: [],
      value2: "",
      options2: [],
      checked1: false,
      checked2: false,
      tableData: [
        {
          ranking: "1",
          salesman: "",
          follow_number: "",
        },
        {
          ranking: "2",
          salesman: "",
          follow_number: "",
        },
        {
          ranking: "3",
          salesman: "",
          follow_number: "",
        },
        {
          ranking: "4",
          salesman: "",
          follow_number: "",
        },
      ],
    });

    // 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("myEcharts"));
      // 把配置和数据放这里
      chart.setOption({
        xAxis: {
          type: "category",
          axisLabel: {
            fontSize: 16,
          },
          data: ["张三", "王立", "李四", "莉莉"],
        },
        tooltip: {
          trigger: "axis",
        },
        yAxis: {
          type: "value",
          min: 0,
          max: 4.5,
          splitNumber: 9,
        },

        series: [
          {
            data: [4, 4, 3, 2],
            barWidth: 50,
            type: "bar",
            // smooth: true  用于折线图中让折线变平滑
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }
    return {
      ...toRefs(state),
      initChart,
    };
  },
};
</script>

<style scoped>
.top {
  margin-left: 30px;
}
.top_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  height: 40px;
}
.top_line > p {
  margin-left: 30px;
}
.el-select,
.choose {
  margin-left: 30px;
}
/* #myEcharts {
    margin-left: ;
} */
.footer {
  margin-left: 20px;
}
.footer_title {
  display: flex;
}
.footer_main {
  margin: 0 20px 20px 0;
}
.last {
  text-align: center;
  margin-bottom: 10px;
}
</style>
