<template>
  <!-- 按时间汇总 -->
  <div class="top">
    <div class="top_line">
      <div>时间维度：</div>
      <p>日</p>
      <p>周</p>
      <p>月</p>
      <p>季</p>
      <p>年</p>
    </div>
    <div class="top_line">
      <div>时间段:</div>
      <div>
        <el-date-picker
            size="mini"
            v-model="time_slot"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            >
        </el-date-picker>
      </div>
    </div>
    <div class="top_line">
      <div>业务员</div>
      <div>
        <el-select size="mini" v-model="value1" placeholder="请选择">
          <el-option
            v-for="item in options1"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        <el-select size="mini" v-model="value2" placeholder="请选择">
          <el-option
            v-for="item in options2"
            :key="item.value2"
            :label="item.label"
            :value="item.value2"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="top_line">
      <el-checkbox v-model="checked1" label="只显示有数据的时间"></el-checkbox>
    </div>
  </div>
  
  <div id="return_myEcharts2" :style="{ width: '900px', height: '400px' }"></div>
  <div class="footer">
    <div class="footer_title">
      <p>合计：</p>
      <p>销售额：</p>
      <p>￥31000.00</p>
    </div>
    <div class="footer_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%; height: 100%"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="time" label="时间" width="">
        </el-table-column>
        <el-table-column prop="collection_amount" label="回款金额" width="">
        </el-table-column>
      </el-table>
    </div>
  </div>
  <div class="last">共 4 条</div>
</template>
<script>
import { reactive, toRefs, onMounted, onUnmounted } from "vue";
import * as echarts from "echarts";
export default {
  name: "echartsBox",
  components: {},
  setup() {
    const state = reactive({
      value1: "",
      options1: [],
      value2: "",
      options2: [],
      time_slot: "",
      checked1: false,
      tableData: [
        {
          time: "2020/9/1",
          collection_amount: "10000",
        },
        {
          time: "2020/9/2",
          collection_amount: "8000",
        },
        {
          time: "2020/9/3",
          collection_amount: "8000",
        },
        {
          time: "2020/9/4",
          collection_amount: "5000",
        },
      ],
    });

    // 声明定义一下echart
    let echart = echarts;

    onMounted(() => {
      initChart();
    });

    onUnmounted(() => {
      echart.dispose;
    });

    // 基础配置一下Echarts
    function initChart() {
      let chart = echart.init(document.getElementById("return_myEcharts2"));
      // 把配置和数据放这里
      chart.setOption({
        xAxis: {
          type: "category",
          axisLabel: {
            fontSize: 16,
          },
          data: ["2020/9/1", "2020/9/2", "2020/9/3", "2020/9/4"],
        },
        tooltip: {
          trigger: "axis",
        },
        yAxis: {
          type: "value",
          min: 0,
          max: 4500,
          splitNumber: 9,
        },

        series: [
          {
            itemStyle : { normal: {label : {show: true}}}, //显示数值
            data: [3453, 1341, 3411, 4321],
            barWidth: 50,
            type: "line",
            // smooth: true  用于折线图中让折线变平滑
          },
        ],
      });
      window.onresize = function () {
        //自适应大小
        chart.resize();
      };
    }
    return {
      ...toRefs(state),
      initChart,
    };
  },
};
</script>

<style scoped>
.top {
  margin-left: 30px;
}
.top_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  height: 40px;
}
.top_line > p {
  margin-left: 30px;
}
.el-select{
  margin-left: 30px;
}

.footer {
  margin-left: 20px;
}
.footer_title {
  display: flex;
}
.footer_main {
  margin: 0 20px 20px 0;
}
.last {
  text-align: center;
  margin-bottom: 10px;
}
</style>
<style>
</style>